
.app-logo {
  width: 50%;
  min-width: 280px;
  max-height: 20vh;
}

.app-logo-2 {
  min-width: 280px;
  max-height: 30vh;
  margin-bottom: 2vh;
  margin-left: 30px;
}

.go-fullscreen-btn {
  position: absolute;
  top: 5px;
  right: 5px;
  z-index: 999999;
  opacity: 0.5;
}


.main-window {
  text-align: center;
  background-color: #f5f5f5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #333333;
}

.app-title {
  font-size: 1.4em;
  margin: 15px 0 70px 0;
  color: #AAAAAA;
}

.footer-btns-container {
  max-width: 97%;
}
.footer-btns-container.responsive {
  padding: 2px;
  border-radius: 6px;
  background: #80808088;
  position: absolute;
} 
.main-window.portait .footer-btns-container.responsive {
  /* --columns: 2 !important; */
  bottom: 0;
  width: 100%;
} 
.main-window.paysage .footer-btns-container.responsive {
  --columns: 1 !important;
  top: 40%;
  right: 6px;
} 

.app-btn {
  height: 100%;
}
.app-btn span.label {
  margin-left: 4px;
}
.app-btn.transparent {
  background-color: #FFFFFF55;
  color: white;
}

.adm-notice-bar-content-inner {
  font-size: var(--adm-font-size-10);
}

.mask-overlay-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;  
  color: #DADADA;
  font-size: 1.3em !important;
}


@media screen and (max-width: 800px) {
  .app-logo {
    width: 80%;
  }
}
@media screen and (max-width: 300px) {
  .app-logo {
    width: 95%;
    min-width: unset;
  }
}
@media screen and (max-width: 200px) {
  .app-logo {
    width: 95%;
    min-width: unset;
  }
  .app-btn span.label {
    display: none;
  }
}
