
.camera-view {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.camera-view .main-window {
    background-color: black;
    color: white;
}

.camera-preview-container {
    height: 100vh;
    width: 100%;
}

.camera-view .main-window.portait .footer-btns-container.responsive {
    --columns: 3 !important;
} 